<template>
<div class="list-content">
    <div class="no-data text_center none">
      <img src="../assets/img/nodata-order.png" alt="" />
    </div>

    <ul class="order-ul">
      <li class='bg-white weui_cells weui_cells_access msg-list'>
        <a class="weui_cell" href="javascript:;">
                <div class="weui_cell_bd weui_cell_primary">
                    <p class="datetime">2016-07-01 16:23:45</p>
                    <p class='lv'>尊敬的用户，您的订单:  <span class="red">648-321312442452342</span>  客服消息。</p>
                </div>
                <div class="weui_cell_ft">
                </div>
            </a>
      </li>
    </ul>
</div>
</template>

<script>
import $ from '../assets/js/lib/jquery-2.1.4.js'
export default {
  name: 'hello',
  data () {
    return {
      'footer':[{'title':'首页','class':'mui-icon-home','name':'home','active':'mui-active'},{'title':'列表','class':'mui-icon-email','name':'list','active':''},{'title':'我的','class':'mui-icon-contact','name':'my','active':''}]
    }
  },
  methods:{
    active:function(obj){
      this.footer.forEach(function(el,index){
        if(index==obj){
          el.active='mui-active';
        }else{
          el.active='';
        }
      })
    }
  }
}
</script>
<style scoped>
</style>



