<template>
  <div class="charge">
    <div class="bg-white cm-padding charge-user-name"   id='charge' @click='al()'>
      手游用户xxxx
    </div>

    <div class="bg-white cm-padding chrage-panel-wrap" style='margin-top:1px'>
      <p class="title">充值金额：<span class="red" id='currency'>0</span>元</p>
      <ul class="op-wrap text_center overflow">
        <li class="left" :class='el.isOn' v-for="(el,index) in money" @click='chose(index)'>{{el.sum}}</li>
      </ul>
    </div>

    <div class=" cm-padding" style=''>
        <!-- <router-link :to="{name:'ordertopay'}" class="weui_btn weui_btn_warn" @click='charge()'>
          立即充值
        </router-link> -->
        <a class="weui_btn weui_btn_warn" @click='charge()'>
          立即充值
        </a>
    </div>
  </div>
</template>

<script>
// 在index.html已经引入了所有静态文件，下面这两行可以删除了
import $ from'../../assets/js/lib/jquery-2.1.4.js'
import '../../assets/js/lib/jquery-weui.min.js'  //必须修改这个文件在文件中添加jq的文件依赖


import router from '../../router.js'

// $.toast("请选择面额", "forbidden");
export default {
  name: 'hello',
  created(){
  },
  data () {
    return {
      msg: '',
      money:[{'sum':'10元','isOn':''},{'sum':'30元','isOn':''},{'sum':'50元','isOn':''},{'sum':'100元','isOn':''},{'sum':'150元','isOn':''},{'sum':'200元','isOn':''},{'sum':'500元','isOn':''}]
    }
  },
  components:{

  },
  methods:{
    chose:function(index){
      // $('.op-wrap li').removeClass('on')&&$('.op-wrap li').eq(index).addClass('on');   //jq的方式切换选中状态
      this.money.forEach(function(el,index){
        el.isOn=''
      })
      this.money[index].isOn='on';
      this.msg=parseInt(this.money[index].sum);
      $('#currency').html(parseInt(this.money[index].sum));
    },
    charge:function(){
      if(Number($('#currency').text())==0)
      {
        // e.preventDefault();
        $.toast("请选择面额", "forbidden");
      }else{
        router.push({ name: 'ordertopay',params: { money: this.msg }})
      }
    }
  }
}
</script>

<style scoped>

</style>



