<template>
  <div class="order">
    <!-- keep-alive保证子路由页面（不包括后代路由页面）已经存在的数据（不会保存路由传递过去的参数数据），order的子路由的页面的created只会执行一次 -->
  <keep-alive>
    <transition name='fade' mode='out-in'>
      <router-view></router-view>
    </transition>
  </keep-alive>
  </div>
</template>

<script>

export default {
  name: 'hello',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  },components: {

  }
}
</script>
<style>
.fade-enter-active {
  -webkit-transition: all .4s ease-in-out;
}
.fade-leave-active {
  -webkit-transition: all .7s ease;
}
.fade-enter, .fade-leave-active {
  /*-webkit-transform: translateY(100px);*/
  -webkit-transform: scale(0.5);
  opacity: 0;
}
</style>



