<template>
  <div class="orderwrite">
    <form action="/Web/Order/addorder.html" method="post" style='margin-bottom:60px' id="frmOrder">
            <!--账号信息-->
            <div class="weui_cells_title recharge-title">帐号信息</div>
            <div class="weui_cells weui_cells_form weui_cells_access">
                <div class="weui_cell">
                    <div class="weui_cell_hd"><label class="weui_label">登入方式</label></div>
                    <div class="weui_cell_bd weui_cell_primary">
                        <input name="type" v-model='type' type="text" id="type" placeholder="请选择登入方式" class="weui_input weui-s must">
                    </div>
                    <div class="weui_cell_ft"></div>
                </div>
                <div class="weui_cell">
                    <div class="weui_cell_hd"><label class="weui_label">游戏帐号</label></div>
                    <div class="weui_cell_bd weui_cell_primary">
                        <input name="account" v-model='account' type="text" id="account" maxlength="40" placeholder="请输入游戏登录帐号" class="weui_input must">
                    </div>
                </div>
                <div class="weui_cell">
                    <div class="weui_cell_hd"><label class="weui_label">帐号密码</label></div>
                    <div class="weui_cell_bd weui_cell_primary">
                        <input name="password" v-model='password' type="password" id="password" maxlength="40" placeholder="请输入游戏帐号的密码" class="weui_input must">
                    </div>
                    <div class="look_pwd look_pwd_off"></div>
                </div>
                <div class="weui_cell">
                    <div class="weui_cell_hd"><label class="weui_label">确认密码</label></div>
                    <div class="weui_cell_bd weui_cell_primary">
                        <input name="password2" type="password" id="password2" maxlength="40" placeholder="再输一次密码" class="weui_input must">
                    </div>
                </div>
            </div>

            <!--充值信息-->
            <div class="weui_cells_title recharge-title">充值信息</div>
            <div class="weui_cells weui_cells_access black_link">
              <div class="weui_cell">
                    <div class="weui_cell_hd"><label class="weui_label">游戏名称</label></div>
                    <div class="weui_cell_bd weui_cell_primary">
                        <input name="game" v-model='game' type="text" id="game" maxlength="40" placeholder="请输入游戏名称" class="weui_input must">
                    </div>
                </div>
                <div class="weui_cell">
                    <div class="weui_cell_hd"><label class="weui_label">游戏区服</label></div>
                    <div class="weui_cell_bd weui_cell_primary">
                        <input name="area" v-model='area' type="text" id="area" placeholder="请选择游戏区服" class="weui_input weui-s must">
                    </div>
                    <div class="weui_cell_ft"></div>
                </div>
                <div class="weui_cell">
                    <div class="weui_cell_hd"><label class="weui_label">角色名称</label></div>
                    <div class="weui_cell_bd weui_cell_primary">
                        <input name="role" v-model='role' type="text" id="role" maxlength="40" placeholder="请输入角色名称" class="weui_input must">
                    </div>
                </div>
                <div class="weui_cell">
                    <div class="weui_cell_hd"><label class="weui_label">充值面额</label></div>
                    <div class="weui_cell_bd weui_cell_primary">
                        <input name="price" v-model='price' type="text" id="price" maxlength="40" placeholder="请选择充值面额" class="weui_input weui-s must">
                    </div>
                    <div class="weui_cell_ft"></div>
                </div>
                <div class="weui_cell">
                    <div class="weui_cell_hd"><label class="weui_label">充值数量</label></div>
                    <div class="weui_cell_bd weui_cell_primary">
                        <input name="count" v-model='count' type="text" id="count" maxlength="40" placeholder="请输入充值数量" class="weui_input must">
                    </div>
                </div>
                <div class="weui_cell">
                    <div class="weui_cell_hd"><label class="weui_label">订单备注</label></div>
                    <div class="weui_cell_bd weui_cell_primary">
                        <input name="remark" v-model='more' id="remark" type="text" placeholder="非必填，100字以内" class="weui_input">
                    </div>
                </div>
            </div>

            <p style='font-size:12px;padding:15px 15px'>请确认角色ID的正确性，否则因角色ID错误造成的充值不到账，与千手官方无关。</p>
        </form>

        <!--订单结算-->
        <div class="submit-f overflow fixed">
          <div class="left">
            <span class='total'>总计￥{{money}}</span>
            <span class='discount'>|省￥8.64</span>
          </div>
          <span class="right chose" style='color:white'  @click='sub()'>提交订单</span>
          <!-- <router-link :to="{name:'ordercomfirm',params:{money:money,game:game,type:type,account:account,area:area,role:role,more:more,password:password,count:count,total:total}}" class="right chose" style='color:white' >提交订单</router-link> -->
        </div>
  </div>
</template>

<script>
import router from '../../router.js'
export default {
  name: 'hello',
  created(){
    // 清空表单
    // console.log(1)
    // this.price='';this.game='';this.type='';this.account='';this.area='';this.role='';this.more='';this.password='';this.count='';
    // console.log(this.account);
    // 初始化weui的选择器组件
    var me=this;
    setTimeout(function(){
      // 填写订单
      var choseItem={
        data:[{
            item:['官方','QQ','微信','微博'],
            title:'登录方式'
          },{
            item:['电信一区','电信二区','电信三区'],
            title:'选择游戏区服'
          },{
            item:['60元宝','100元宝','200元宝'],
            title:'选择充值面额'
          }],
        select:function(obj,data,idf)
        {
          obj.select({
                title: data.title,
                items: data.item,
                onChange: function(d) {
                  me[idf]=d.values;
                },
                onClose: function(d) {
                },
                onOpen: function(d) {
                },
              });
        }
      };
      $.each($('.weui-s'),function(index, value, array) {
         choseItem.select($(value),choseItem.data[index],$(value).attr('id'));
      });
    },0)


  },
  data () {
    return {
      price:'',game:'',type:'',account:'',area:'',role:'',more:'',password:'',count:''
    }
  },
  computed: {
    money:function(){
      var val=parseInt(this.count)*parseInt(this.price);
      // val? (function(){return val})():(function(){alert(1);return 0})();
      if(val){
        return val;
      }else{
        return 0
      }
      
    }
  },
  methods:{

    sub:function(){
      // 提交表单，验证信息完整性
      var isCompelet=true;
      $.each($('.must'), function(index, val) {
         if($(val).val()==''){
            isCompelet=false;
            // $.toast("请填写完整必要信息", "forbidden");
            mui.alert('请填写完整必要信息')
            return false;
         }
      });
      if(isCompelet){
        router.push({name:'ordercomfirm',params:{money:this.money,game:this.game,type:this.type,account:this.account,area:this.area,role:this.role,more:this.more,password:this.password,count:this.count,price:this.price}})
      }
    }

  }
}
</script>
<style>
input[type=color],input[type=date],input[type=datetime-local],input[type=datetime],input[type=email],input[type=month],input[type=number],input[type=password],input[type=search],input[type=tel],input[type=text],input[type=time],input[type=url],input[type=week],select,textarea {
    line-height: 21px;
    width: auto;
    height: auto;
    margin-bottom: 0px;
    padding: 0px 0px;
    -webkit-user-select: text;
    border: 0px solid rgba(0,0,0,.2);
    border-radius: 3px;
    outline: 0;
    background-color: transparent;
    -webkit-appearance: none;
}

</style>
<style>
  h1{
  margin:0;
}
</style>



