<template>
  <div class="slider">
    <div class="swiper-container">
      <!-- Additional required wrapper -->
      <div class="swiper-wrapper">
        <!-- Slides -->
        <div class="swiper-slide"><img src="../assets/img/banner.jpg" style='width: 100%;'/></div>
        <div class="swiper-slide"><img src="../assets/img/banner.jpg" style='width: 100%;'/></div>
      </div>
      <!-- If we need pagination -->
      <div class="swiper-pagination"></div>
    </div>
  </div>
</template>

<script scoped>
export default {
  name: 'slider',
  created(){

    // window.onload=function(){
    setTimeout(function(){
      $(".swiper-container").swiper({
        loop: true,
        autoplay: 3000
      });
    }, 0)
      
    // }
     
  },
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  }
}
</script>

