                  <div class="weui_cell_bd weui_cell_primary">
                      <input class="weui_input" id="phoneNum" name="phoneNum" maxlength="11" type="tel" pattern="[0-9]*" placeholder="请输入手机号码">
                  </div>
              </div>
              <div class="weui_cell">
                  <div class="weui_cell_hd"><label class="weui_label">验证码</label></div>
                  <div class="weui_cell_bd weui_cell_primary">
                      <input class="weui_input" id="autoCode" name="autoCode" type="text" maxlength="6" pattern="[0-9]*" placeholder="请输入验证码">
                  </div>
                  <div class="weui_cell_ft">
                      <input id="send-code" name="send-code" type="button" class="weui_btn weui_btn_mini weui_btn_warn" value="获取验证码">
                  </div>
              </div>
              <div class="weui_cell">
                  <div class="weui_cell_hd"><label class="weui_label">输入密码</label></div>
                  <div class="weui_cell_bd weui_cell_primary">
                      <input class="weui_input" id="password1" name="password1" type="password" maxlength="18" placeholder="6-18位数字、字母或符号">
                  </div>
              </div>
              <div class="weui_cell">
                  <div class="weui_cell_hd"><label class="weui_label">确认密码</label></div>
